.text {
  font-family: "Montserrat", Arial;
  color: #4e4e4e;
  text-shadow: 0 0 0 #353535, 0 0 1px transparent;
  line-height: 30px;
}

.text--extra-small {
  font-size: 10px;
}

.text--small {
  font-size: 12px;
}

.text--medium {
  font-size: 14px;
}

.text--large {
  font-size: 18px;
}

.text--extra-large {
  font-size: 22px;
}

.text--center {
  text-align: center;
}

.text--right {
  text-align: right;
}

.text--left {
  text-align: left;
}

.text--inverted {
  color: #fff;
  text-shadow: 0 0 0 #fff, 0 0 1px transparent;
}

.text--bold {
  font-weight: 600;
}

.text--full {
  width: 100%;
}

.text--primary {
  color: #4e4e4e;
  text-shadow: 0 0 0 #353535, 0 0 1px transparent;
}

.text--secondary {
  color: #7E7E7E;
  text-shadow: 0 0 0 #353535, 0 0 1px transparent;
}

.text--tertiary {
  color: #da3688;
  text-shadow: 0 0 0 #353535, 0 0 1px transparent;
}

.text--error {
  font-size: 10px;
  color: #DA4453;
  margin-top: 2px;
  line-height: 14px;
  flex-grow: 1;
  min-width: 50%;
  min-height: 16px;
}

.text--uppercase {
  text-transform: uppercase;
}

.button {
  display: flex;
  align-items: center;
  height: 36px;
  font-size: 14px;
  font-family: "Montserrat", Arial;
  padding: 4px 8px;
  border-radius: 3px;
  border: 0;
  margin: 0;
  justify-content: center;
  cursor: pointer;
}

.button span {
  flex-grow: 1;
}

.button.is-disabled {
  background-color: #BFC3C6;
  color: #818181;
  border: none;
  cursor: no-drop;
  transition: background-color, color, 0.2s linear;
}

.button.is-disabled svg {
  fill: #818181;
  transition: fill, 0.2s linear;
}

.button.is-disabled:focus {
  border: 0;
  box-shadow: none;
}

.button.is-disabled:hover {
  background-color: #BFC3C6;
}

.button.is-loading {
  cursor: no-drop;
  transition: background-color, 0.2s linear;
}

.button.is-loading:focus {
  border: 0;
  box-shadow: none;
}

.button--primary {
  background-color: #3c6dec;
  color: #fff;
  font-weight: 600;
  min-width: 100px;
}

.button--primary svg {
  fill: #fff;
}

.button--primary:focus {
  outline: none;
  border: 1px solid #fff;
  box-shadow: 0 0 0 2px #3c6dec;
}

.button--primary:hover {
  background-color: #113eb1;
  transition: background-color 0.2s linear;
}

.button--primary .is-loading {
  background-color: #6a90f1;
}

.button--primary .is-loading:hover {
  background-color: #6a90f1;
}

.button--secondary {
  background-color: #fff;
  color: #3c6dec;
  font-weight: 500;
  border: 1px solid #3c6dec;
}

.button--secondary svg {
  fill: #3c6dec;
}

.button--secondary:focus {
  outline: none;
  border: 1px solid #fff;
  box-shadow: 0 0 0 2px #3c6dec;
}

.button--secondary:hover {
  background-color: #dfe7fc;
  transition: background-color 0.2s linear;
}

.button--tertiary {
  background-color: #ffba3a;
  color: #1346c8;
  font-weight: 500;
}

.button--tertiary svg {
  fill: #1346c8;
}

.button--tertiary:focus {
  outline: none;
  border: 1px solid #fff;
  box-shadow: 0 0 0 2px #1346c8;
}

.button--tertiary:hover {
  background-color: #ed9a00;
  transition: background-color 0.2s linear;
}

.button--full {
  width: 100%;
}

.button--icon {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 0;
  border-radius: 3px;
  padding: 2px;
  color: #4e4e4e;
  cursor: pointer;
  background-color: unset;
  height: unset;
}

.button--icon svg {
  vertical-align: middle;
  pointer-events: none;
}

.button--icon:hover svg {
  fill: #282828;
}

.button--form {
  margin-bottom: 18px;
}

.box {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  border: 2px solid #D2d6d9;
  border-radius: 3px;
  text-decoration: none;
}

.box__container {
  display: flex;
  flex-wrap: wrap;
  background-color: #fff;
  padding: 8px;
  width: 100%;
}

.box__container--inverted {
  background-color: #f9f9f9;
  border-radius: 3px;
}

.box__footer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 8px;
  background-color: #f3f4f6;
  width: 100%;
}

.box__container--form {
  min-height: 300px;
}

.box--shadow {
  box-shadow: 2px 2px 20px 2px rgba(0, 0, 0, 0.1);
  border: none;
  background-color: #fff;
}

.box--no-border {
  border: none;
}

.box__container--hover:hover {
  background-color: #fafafa;
}

@media only screen and (min-width: 768px) {
  .box--float {
    margin-top: -35px;
  }
}

