.input {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  position: relative;
}

.input.has-error .input__display {
  background-color: #DA4453;
  color: #fff;
  transition: background-color, 0.2s linear;
}

.input.has-error .input__display svg {
  fill: #fff;
  transition: fill, 0.2s linear;
}

.input.has-error .input__item {
  border: 2px solid #DA4453;
  transition: border, 0.2s linear;
}

.input.has-error svg {
  fill: #DA4453;
  transition: fill, 0.2s linear;
}

.input.has-error .input__textarea {
  border: 2px solid #DA4453;
}

.input.has-error .pill__item svg {
  fill: #3c6dec;
}

.input.has-error .pill {
  border-left: 2px solid #DA4453;
  border-right: 2px solid #DA4453;
  border-bottom: 2px solid #DA4453;
}

.input.has-success .input__display {
  background-color: #8CC152;
  color: #fff;
  transition: background-color, 0.2s linear;
}

.input.has-success .input__display svg {
  fill: #fff;
  transition: fill, 0.2s linear;
}

.input.has-success .input__item {
  border: 2px solid #8CC152;
  transition: border, 0.2s linear;
}

.input.has-success svg {
  fill: #8CC152;
  transition: fill, 0.2s linear;
}

.input__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 25px;
  height: 100%;
  font-family: "Montserrat", Arial;
  font-weight: 500;
  font-size: 12px;
  fill: #a0a9af;
  top: 50%;
  transform: translate(0%, -50%);
}

.input__display {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  background-color: #fbfcfc;
  top: 0;
  width: 26px;
  height: 32px;
  padding: 0 4px;
  margin: 2px;
  font-family: "Montserrat", Arial;
  font-weight: 500;
  font-size: 12px;
}

.input__display--right {
  right: 0;
  border-radius: 0 1px 1px 0;
  padding: 0 4px;
}

.input__display--left {
  left: 0;
  border-radius: 1px 0 0 1px;
  padding: 0 4px;
}

.input__info {
  position: relative;
  width: 100%;
  display: flex;
}

.input__info--right .input__item {
  padding-right: 38px;
  padding-left: 4px;
}

.input__info--left .input__item {
  padding-left: 38px;
}

.input__info--both .input__item {
  padding: 0  38px;
}

.input__textarea {
  width: 100%;
  border-radius: 3px;
  border: 2px solid #D2d6d9;
  padding: 0 4px;
  font-family: "Montserrat", Arial;
  font-size: 12px;
  box-sizing: border-box;
}

.input__textarea::-webkit-input-placeholder {
  color: #dadee0;
}

.input__textarea:-moz-placeholder {
  color: #dadee0;
}

.input__textarea::-moz-placeholder {
  color: #dadee0;
}

.input__textarea:-ms-input-placeholder {
  color: #dadee0;
}

.input__textarea:hover {
  border: 2px solid #ffba3a;
  transition: border 0.2s linear;
}

.input__textarea:focus {
  border: 2px solid #3c6dec;
  outline: none;
}

.input__item {
  width: 100%;
  height: 36px;
  border-radius: 3px;
  border: 2px solid #D2d6d9;
  padding: 0 4px;
  font-family: "Montserrat", Arial;
  font-size: 12px;
  box-sizing: border-box;
  text-shadow: 0 0 0 #000000, 0 0 1px transparent;
}

