}

.input__item::-moz-placeholder {
  color: #D2d6d9;
}

.input__item:-ms-input-placeholder {
  color: #D2d6d9;
}

.input__item:hover {
  border: 2px solid #ffba3a;
  transition: border 0.2s linear;
}

.input__item:focus {
  border: 2px solid #3c6dec;
  outline: none;
}

.align--left {
  display: flex;
  justify-content: flex-start;
}

.align--right {
  display: flex;
  justify-content: flex-end;
}

.align--center {
  display: flex;
  justify-content: center;
}

.align--top {
  align-self: flex-start;
  align-items: flex-start;
}

.align--middle {
  align-self: center;
  align-items: center;
}

.align--bottom {
  align-self: flex-end;
  align-items: flex-end;
}

.image {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.image--extra-small {
  max-width: 80px;
  max-height: 60px;
}

.image--small {
  max-width: 160px;
  max-height: 120px;
}

.image--medium {
  max-width: 240px;
  max-height: 180px;
}

.image--large {
  max-width: 360px;
  max-height: 270px;
}

.image--extra-large {
  max-width: 640px;
  max-height: 480px;
}

.image--border {
  border: 2px solid #D2d6d9;
}

.hamburguer {
  display: flex;
}

@media only screen and (min-width: 992px) {
  .hamburguer {
    display: none;
  }
}

.plans {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  background-color: #EAEFF3;
}

/* Classes padrões */
.icon {
  display: flex;
}

.icon--primary {
  fill: #a7a7a7;
}

.icon--primary svg {
  fill: #a7a7a7;
}

.icon--secondary {
  fill: #ffba3a;
}

.icon--secondary svg {
  fill: #ffba3a;
}

.icon--tertiary {
  fill: #da3688;
}

.icon--tertiary svg {
  fill: #da3688;
}

.icon--inverted {
  fill: #fff;
}

.icon--inverted svg {
  fill: #fff;
}

.icon--extra-small {
  width: 8px;
  height: 8px;
}

.icon--extra-small svg {
  width: 8px;
  height: 8px;
}

.icon--small {
  width: 16px;
  height: 16px;
}

.icon--medium {
  width: 24px;
  height: 24px;
}

.icon--large {
  width: 32px;
  height: 32px;
}

.icon--extra-large {
  width: 48px;
  height: 48px;
}

.waves {
  background-size: 103%;
  background-repeat: repeat-x;
  background-position-x: -10px;
  display: flex;
  width: 100%;
  height: 100%;
  left: -1px;
}

.waves--header {
  padding-bottom: 5%;
  background-image: url("../../images/icons/curva.svg");
  background-position: top;
  margin-top: -2px;
}

.waves--plans {
  background-image: url("../../images/icons/curva2.svg");
  padding-bottom: 14%;
  background-position: bottom;
  margin-bottom: -2px;
}

.waves--footer {
  background-image: url("../../images/icons/curva4.svg");
  padding-bottom: 10%;
  margin-top: -2px;
  background-position-y: -1px;
}

.title {
  width: 100%;
  font-family: "Montserrat", Arial;
  margin: 0;
  font-weight: 600;
  line-height: 1.3;
}

.title--extra-small {
  font-size: 16px;
}

.title--small {
  font-size: 20px;
}

.title--medium {
  font-size: 24px;
}

.title--large {
  font-size: 30px;
}

.title--extra-large {
  font-size: 38px;
}

.title--center {
  text-align: center;
}

.title--right {
  text-align: right;
}

.title--left {
  text-align: left;
}

.title--primary {
  color: #0A2466;
}

.title--secondary {
  color: #7E7E7E;
}

.title--tertiary {
  color: #da3688;
}

.title--inverted {
  color: #fff;
}

.header {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  background-color: #3c6dec;
}

.header__box {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
  padding: 8px 16px;
}

.footer {
  background-color: #3c6dec;
}

.footer .title {
  text-align: center;
}

.footer .text {
  width: 100%;
  text-align: center;
}

.footer .link {
  width: 100%;
  justify-content: center;
}

@media only screen and (min-width: 768px) {
  .footer .title {
    text-align: unset;
  }
  .footer .text {
    width: unset;
    text-align: unset;
  }
  .footer .link {
    width: unset;
    justify-content: unset;
  }
}

.navbar {
  display: none;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  box-sizing: border-box;
  position: absolute;
  top: 85px;
  left: 0px;
  z-index: 200;
  width: 100%;
  background-color: #274697;
}

.navbar.is-active {
  display: flex;
}

@media only screen and (min-width: 992px) {
  .navbar {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    flex-grow: 5;
    position: unset;
    background-color: unset;
    width: unset;
  }
}

.navbar__item {
  display: flex;
  align-items: center;
  padding: 16px;
}
