.box{
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  border: $border--medium $color-line-system;
  border-radius: $border--radius;
  text-decoration: none;
}

.box__container{
  display: flex;
  flex-wrap: wrap;
  background-color: $background--clean;
  padding: $space--medium;
  width: 100%;
}

.box__container--inverted{
  background-color: $background--extra-light;
  border-radius: $border--radius;
}

.box__footer{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: $space--medium;
  background-color: $background--light;
  width: 100%;
}

.box__container--form{
   min-height: 300px;
}

.box--shadow{
  box-shadow: $shadow;
  border: none;
  background-color: $background--clean;
}

.box--no-border{
  border: none;
}

.box__container--hover{
  &:hover{
    background-color: #fafafa;
  }
}

.box--float{
  @media #{$tablets} {
    margin-top: -35px;
  }
}
